.homepage {
  height: calc(100vh - 5rem);
  margin: 2.5rem;
  background-image: linear-gradient(
      rgba(36, 42, 46, 0.8),
      rgba(36, 42, 46, 0.8)
    ),
    url("../../public/bg.jpg");
  background-size: cover;
  background-position: center;
  padding: 2.5rem 5rem;
}

.homepage section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 90%;
  gap: 1.6em;
}

.homepage h1 {
  font-size: 4.4rem;
  line-height: 1.3;
  letter-spacing: -0.5px;
}

.homepage h2 {
  font-size: 2rem;
  color: var(--color-light--1);
  margin-bottom: 2.4rem;
}

@media (max-width: 49em) {
  /* 784px */
  .homepage h1 {
    font-size: 3.6rem;
  }
}
